<template>
  <div class="NDT-Page">
    <h1 class="NDT-Page__Header">
      <span>Seatmap</span>
      <a href="#" @click.prevent="maximize">
        <icon icon="vector-square" />
      </a>
    </h1>
    <div class="NDT-Page__Body" ref="seatmap">
      <ndt-seatmap read-only :explanation="false" />
    </div>
  </div>
</template>

<script>
  import NdtSeatmap from '../components/NdtSeatmap'
  export default {
    name: 'Seatmap',

    components: {
      NdtSeatmap
    },

    methods: {
      maximize () {
        const el = this.$refs.seatmap
        el.requestFullscreen ? el.requestFullscreen() : el.webkitRequestFullScreen()
      }
    }
  }
</script>

